import * as React from 'react';
import { useStaticQuery, graphql, PageProps } from 'gatsby';
import { constructUrl } from '../utils/path';
import { getSrc } from 'gatsby-plugin-image';

const Seo = ({ description, title, image, children }: PageProps) => {
  const { site, ogImageDefault }: Queries.SeoQuery = useStaticQuery(
    graphql`
      query Seo {
        site {
          siteMetadata {
            title
            description
            social {
              twitter
            }
            siteUrl
          }
        }

        ogImageDefault: file(relativePath: { eq: "og-icon.png" }) {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, quality: 90)
          }
        }
      }
    `,
  );

  const ogImageUrl = constructUrl(
    site.siteMetadata?.siteUrl,
    image ?? getSrc(ogImageDefault?.childImageSharp?.gatsbyImageData),
  );

  const metaDescription = description || site.siteMetadata.description;
  const defaultTitle = site.siteMetadata?.title;

  return (
    <>
      <title>{defaultTitle ? `${title} | ${defaultTitle}` : title}</title>
      <meta name="description" content={metaDescription} />
      <meta property="og:title" content={title} />
      <meta property="og:description" content={metaDescription} />
      <meta property="og:type" content="website" />
      <meta property="og:image" content={ogImageUrl} />
      <meta name="twitter:image" content={ogImageUrl} />
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:creator" content={site.siteMetadata?.social?.twitter || ``} />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={metaDescription} />
      {children}
    </>
  );
};

export default Seo;
